.ContactUs {
  .registerInfoField {
      padding: 40px 20px;
      width: 100%;
  }
  .contactUsImage {
      width: 100%;
      height: 350px;
  }
  .contactUsForm {
      width: 50%;
      padding-right: 20px;
    .contactUsFormText{
        margin-bottom: 47px;
        text-transform: uppercase;
    }
    .textarea {
        margin-bottom: 20px;
        .area::placeholder{
            text-transform: uppercase !important;
            color: #CCCCCC;
            &.darkMode::placeholder {
              color:#8B8F9A!important;
              text-transform: uppercase !important;
            }
        }
    }
    > .flexible {
        margin-bottom: 20px;
        > .TextFieldComponent {
            &:first-child {
                padding-right: 10px;
            }
            &:last-child {
                padding-left: 10px;
            }
        }
    }
  }
  .contactUsInfo {
    width: 50%;
    padding-left: 20px;
    display: flex;
    align-items: center;
    .contactUsInfoContent {
      .contactUsInfoText {
      display: block;
      margin-bottom: 20px;
      text-transform: uppercase;
      }
      .contactsBlock{
        padding: 20px 20px 20px 0px ;
      }
    }
    .infoLocation {
      margin-right: 50px;
      // margin-bottom: 20px;
    }
    .socialBlock {
      .Text {
          text-transform: uppercase;
          font-weight: 500;
          margin-right: 15px;
      }
      .Icon {
          margin:0 15px;
      }
    }
  }
  .contactUsMap {
    position: relative;
    height: 432px;
    width: 100%;
  }
}

@media screen and (max-width: 900px){
  .content-media {
    flex-direction: column-reverse;
    .contactUsForm {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding-left: 25px;
    }
    .contactUsInfo {
      width: 100%;
      padding-bottom: 70px;
      align-items: flex-start;
      padding-left: 25px;
    }
  }
}

@media screen and (max-width: 630px){
  .content-media {
    flex-direction: column-reverse;
    .contactUsForm {
        width: 100%;
        padding-left: 25px;
      .contactUsFormText {
        text-align: center;
        margin-left: 10px;
      }
      .contactUsFormTextFields {
        width: 100%;
        display: flex;
        flex-direction: column;
      }
      .textarea {
          margin-left: 10px;
      }
      .send-button {
        justify-content: center;
      }
      > .flexible {
        > .TextFieldComponent {
          &:first-child {
              padding:0;
              margin-left: 10px;
              margin-bottom: 20px;
          }
          &:last-child {
              padding:0;
              margin-left: 10px;
          }
        }
      }
  }
  .contactUsInfo {
    width: 100%;
    align-items: center;
    padding-bottom: 70px;
    .infoLocation {
      margin-right: 0;
    }
    .socialBlock {
      display: flex;
      flex-direction: column;
      margin-top: 20px;
    }
    .socialBlockText {
      padding-bottom: 20px;
    }
    .contactUsInfoText {
       text-align: center;
    }
    .contactsBlock {
      display: flex;
      align-items: center;
      flex-direction: column;
      .mediaText {
          display: flex;
          margin-bottom: 20px;
      }
    }
  }
  }
}
